html,
body,
#root,
#root > div {
  padding: 0;
  margin: 0;
  height: 100%;
}

#root > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#root > div > div {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

#root > div > span {
  display: -webkit-flex;
  display: flex;
}

#root > div > span > input {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

h1 {
  text-align: center;
  margin: 0;
  padding: 0;
}

input {
  outline: none;
  border: none;
  background-color: black;
  color: #0f0;
  padding: 1em 0.5em;
  display: block;
  font-size: 1.5em;
  -webkit-flex-grow: 1;
  flex-grow: 1;
}

label {
  display: block;
  padding: 1em 0.5em;
  font-size: 1.5em;
}

div.messages {
  margin-left: 1em;
}

form {
  display: -webkit-flex;
  display: flex;
}

.connected {
  background-color: black;
  color: #0f0;
  font-family: verdana;
}

.connecting {
  background-color: red;
  color: yellow;
}

